const ios = /iPad|iPhone/.test(navigator.userAgent);
const availableConcurrency = 'hardwareConcurrency' in navigator ? navigator.hardwareConcurrency : ios ? 2 : 8;
const workers = Array.from({ length: availableConcurrency }, _ => new Worker('./worker.js'));
const numberFormatter = new Intl.NumberFormat();
const numberFormat = number => numberFormatter.format(number);
const loadWords = async url => fetch(url).then(res => res.text());

const {
    customWordFileElem,
    customWordListElem,
    elapsedTimeElem,
    fiveLetterWordCountElem,
    normalizedWordCountElem,
    workersWorkingElem,
    workItemsElem,
    solutionCountElem,
    solutionsElem,
    wordCountElem,
    solveFormElem,
    solveButtonElem,
} = window;

for (let n = 0; n < availableConcurrency; ++n) {
    const option = document.createElement('option');
    option.append(n + 1);
    solveFormElem.concurrency.append(option);
}
solveFormElem.concurrency.value = availableConcurrency;

customWordFileElem.addEventListener('change', e => (solveFormElem.wordlist.value = 'custom'));
customWordListElem.addEventListener('change', e => {
    if (solveFormElem.wordlist.value === 'custom' && !customWordFileElem.value) {
        customWordFileElem.click();
    }
});

const solve = wordsString => {
    const startTime = Date.now();
    solveButtonElem.innerText = 'Solving...';
    solutionsElem.innerHTML = '';
    solutionCountElem.innerText = '0';
    const allWords = wordsString.split(/\r?\n/).map(word => word.trim().toLowerCase());
    wordCountElem.innerText = numberFormat(allWords.length);

    const fiveLetterWords = allWords.filter(word => /^[a-z]{5}$/.test(word));
    fiveLetterWordCountElem.innerText = numberFormat(fiveLetterWords.length);
    // normalizedWords are fiveLetterWords with their letters rearranged so that they are sorted (WORDS becomes DORSW).
    // Anagrams are removed from consideration by searching for normalizedWords instead of actual words since all anagrams
    // map to the same normalizedWord.  Normalized words with any repeating characters /(.)\1/ are also removed.
    const normalizedWordMap = new Map();
    for (const word of fiveLetterWords) {
        const normalizedWord = [...word].sort().join('');
        if (!normalizedWord.match(/(.)\1/)) {
            const entry = normalizedWordMap.get(normalizedWord);
            if (!entry) {
                normalizedWordMap.set(normalizedWord, word);
            } else {
                normalizedWordMap.set(normalizedWord, [entry, word].join('/'));
            }
        }
    }

    normalizedWordCountElem.innerText = numberFormat(normalizedWordMap.size);
    const words = [...normalizedWordMap.keys()];

    const options = words.reduce((options, word) => {
        options[word] = word.split('').map(letter => `letter${letter}`);
        return options;
    }, {});

    const solutions = new Set();
    const displaySolutions = [];
    const a = 'a'.charCodeAt(0);
    const z = 'z'.charCodeAt(0);
    const work = [];

    for (let charCode = a; charCode <= z; ++charCode) {
        const excludedLetter = String.fromCharCode(charCode);
        const includedOptions = Object.fromEntries(Object.entries(options).filter(([word]) => !word.includes(excludedLetter)));
        work.push({ options: includedOptions, givenOptions: [] });
    }

    work.sort((a, b) => Math.random() - 0.5);
    const recordSolution = solution => {
        const normalizedSolution = solution.sort().join(',');
        if (!solutions.has(normalizedSolution)) {
            solutions.add(normalizedSolution);
            solutionCountElem.innerText = numberFormat(solutions.size);
            displaySolutions.push(
                normalizedSolution
                    .split(',')
                    .map(step => normalizedWordMap.get(step))
                    .sort()
                    .join(', ')
            );
        }
    };

    const renderInterval = setInterval(
        _ =>
            requestAnimationFrame(_ => {
                for (const solution of displaySolutions.slice(solutionsElem.children.length)) {
                    const li = document.createElement('li');
                    li.innerText = solution;
                    solutionsElem.append(li);
                }
                const elapsedSeconds = numberFormat((Date.now() - startTime) / 1000);
                elapsedTimeElem.innerText = `${elapsedSeconds} seconds`;
                solutionsElem.scrollTop = solutionsElem.scrollHeight;
            }),
        60
    );

    let currentWorkItem = 0;
    let completedWorkItems = 0;

    const concurrency = solveFormElem.concurrency.value;

    return new Promise((resolve, reject) => {
        for (const [n, worker] of workers.entries()) {
            if (n >= concurrency) {
                break;
            }
            workersWorkingElem.value = n + 1;
            const messageHandler = msg => {
                switch (msg.data.type) {
                    case 'solution': {
                        recordSolution(msg.data.solution);
                        break;
                    }

                    case 'completed': {
                        if (++completedWorkItems < work.length) {
                            if (currentWorkItem < work.length) {
                                worker.postMessage({ type: 'solve', ...work[currentWorkItem++] });
                            } else {
                                workersWorkingElem.value -= 1;
                                worker.removeEventListener('message', messageHandler);
                            }
                        } else if (completedWorkItems === work.length) {
                            const elapsedSeconds = (Date.now() - startTime) / 1000;
                            worker.removeEventListener('message', messageHandler);
                            workersWorkingElem.value -= 1;
                            elapsedTimeElem.innerText = `${numberFormat(elapsedSeconds)} seconds`;
                            solveButtonElem.disabled = false;
                            solveButtonElem.innerHTML = 'Solve';
                            clearInterval(renderInterval);
                            resolve();
                        }
                        workItemsElem.value = work.length - completedWorkItems;
                        break;
                    }
                }
            };

            if (currentWorkItem < work.length) {
                worker.addEventListener('message', messageHandler);
                worker.postMessage({ type: 'solve', ...work[currentWorkItem++] });
            }
        }
    });
};

solveButtonElem.addEventListener('click', async e => {
    e.preventDefault();
    solveButtonElem.disabled = true;
    solveButtonElem.innerText = 'Loading...';
    const formData = Object.fromEntries([...new FormData(solveFormElem)]);
    if (formData.wordlist === 'custom') {
        var reader = new FileReader();
        reader.onload = e => solve(event.target.result);
        reader.readAsText(formData.customWordFile);
    } else {
        loadWords(formData.wordlist).then(solve);
    }
});
